@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,800|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color: #5cdb95;
}
.main-wrapper{

}
.main-wrapper h1{
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
}
.main-wrapper p{
  font-family: 'Roboto', sans-serif;
  font-size: 1.5em;
  line-height: 1.5em;
}
.header{
  text-align: center;
  vertical-align: middle;
  margin-top: 100px;
  height: 100vh;
}
.section h1{
  color:"#05386b" !important;
}
.section{
  margin-top: 100px;
  margin-bottom: 100px;
}

.section p{
  font-size: 21px;
}
h1,h2,h3,h4,h5,p{
  color: #edf5e1;
}

.icons{
  font-size: 20px;
  color: #5cdb95;
}

.biohack-winners{
  padding: 30px;
  border-radius: 20px;
  background: #05386b;
  text-align: center;
  margin-bottom: 40px;
}

.biohack-winners p{
  font-size: 16px;
}

.biohack-winners img{
  width: 255px;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 20px;
}

.contact{
  padding: 40px;
  background: #05386b;
}
.contact h5{
  color: #5cdb95;
}

.contact a{
  padding: 10px;
  background: #05386b;
  border-radius: 50px;
  color: #5cdb95;
  font-size: 40px;
}

.contact a:hover{
  background: #5cdb95;
  color: #05386b;
}
.App {
  text-align: center;
}

/* .background { */
  /* color: #93c48b; */
/* } */

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.content-section{
  padding: 35px;
  border-radius: 20px;
  background: #05386b;
  text-align: center;
  margin-bottom: 40px;
}

.content-section p{
  font-size: 17px;
}

.virus{
  height : 500px;
  width: 650px;
}

.cheeringVirus{
  height : 400px;
  width: 400px;
}

.thinking-virus {
  height: 400px;
  width: 400px;
}

